@media only screen and (max-width: 992px) {
  .main__buttons_orange {
    display: inline;
  }
}

.main p {
  font-size: 16px;
  color: #5d6770;
  padding-left: 210px;
  max-width: 500px;
  display: block;
}

@media only screen and (max-width: 992px) {
  .main p {
    padding-left: 0;
    text-align: center;
    margin: 0 auto;
    display: block;
    width: 220px;
    font-size: 13px;
    margin-bottom: 54px;
  }
}

@media only screen and (max-width: 320px) {
  .main p {
    line-height: 17px;
    letter-spacing: 0.01em;
  }
}

.main p:before {
  content: '*';
  position: absolute;
  left: 210px;
  color: #ff591f;
}

@media only screen and (max-width: 992px) {
  .main p:before {
    display: none;
  }
}

.main p:after {
  content: '';
  width: 150px;
  height: 1px;
  background: #ced3d7;
  position: absolute;
  left: 15px;
}

@media only screen and (max-width: 992px) {
  .main p:after {
    display: none;
    width: 0;
    height: 0;
  }
}

.main__book-section {
  position: relative;
}

.main__book-section img {
  margin-left: 25px;
  box-shadow: 20px 20px 40px -15px rgba(0, 0, 0, 0.6);
}

@media only screen and (max-width: 992px) {
  .main__book-section img {
    margin-left: 50%;
    transform: translateX(-50%);
  }
}

@media only screen and (max-width: 768px) {
  .main__book-section img {
    width: 88%;
  }
}

.blue-triangle {
  position: absolute;
  left: -50px;
  bottom: -130px;
  width: 625px;
  height: 625px;
}

@media only screen and (max-width: 1200px) {
  .blue-triangle {
    left: 0;
    bottom: -160px;
  }
}

@media only screen and (max-width: 992px) {
  .blue-triangle {
    left: 50px;
    bottom: 90px;
  }
}

@media only screen and (max-width: 768px) {
  .blue-triangle {
    top: 0;
    left: 0;
    transform: scale(0.8);
  }
}

@media only screen and (max-width: 540px) {
  .blue-triangle {
    left: -100px;
    transform: scale(0.62);
    top: -60px;
  }
}

@media only screen and (max-width: 380px) {
  .blue-triangle {
    left: -140px;
    top: -125px;
  }
}

.footer {
  width: 100%;
  text-align: center;
  padding: 35px 0;
  position: relative;
}

@media only screen and (max-width: 992px) {
  .footer {
    border-top: 1px solid #ced3d7;
    padding: 20px 0;
  }
}

.footer__copyright {
  width: 100%;
  font-size: 18px;
  color: #5d6770;
  font-family: "Roboto-Light", sans-serif;
}

@media only screen and (max-width: 992px) {
  .footer__copyright {
    font-size: 14px;
  }
}
